// ============================================================
// Layout with Subnav
// ============================================================

.layout-subnav {
  $breakpoint: em(800);
  align-items: baseline;

  > .gi {

    @include mq-min($breakpoint) {

      &:first-child {
        @include grid-item(3, 1);
        order: 1; }

      &:last-child {
        @include grid-item(3, 2);
        display: block !important;
        order: 2; } } } }
