// ============================================================
// Base Typography
// ============================================================

body {
  font-family: $sans-serif;
  line-height: 1.6; }

p {
  color: $gray;
  margin: 0 0 1em;
  font-family: $monospace;
  font-size: rem(14); }

strong {
  font-weight: $bold; }

em {
  font-style: italic; }

pre {
  background: $dark-gray;
  border-radius: $border-radius;
  padding: rem(20) rem(20) rem(25);
  color: $white;
  margin-bottom: rem(16);
  overflow: scroll;

  code {
    background: none;
    font-size: rem(14); } }

code {
  font-family: $monospace;
  font-size: rem(14);
  background: $light-yellow;
  padding: rem(4); }


blockquote {
  border-left: 1px solid $light-gray;
  padding-left: rem(30);

  p {
    font-style: italic; }

  footer {} }


// ------------------------------------------------------------
// Headings
// ------------------------------------------------------------

h1, h2, h3, h4, h5, h6 {
  font-family: $sans-serif; }

.content-area {
  max-width: rem(800);

  h1, h2, h3, h4, h5, h6 {

    &:not(:first-child) {
      padding-top: 2em; } }

  h1 {
    font-size: rem(24);
    line-height: 1.09;
    margin: 0 0 em(12);

    @include mq-min(em(600)) {
      font-size: rem(27); }

    @include mq-min(em(1080)) {
      font-size: rem(33); } }

  h2 {
    color: $green;
    font-size: em(20);
    line-height: 1.125;
    margin: 0 0 em(16);
    border-bottom: 1px solid $off-white;
    padding-bottom: rem(11);

    @include mq-min(em(1080)) {
      font-size: rem(27); } }

  h2 + h3 {
    &:not(:first-child) {
      padding-top: 1em; } }

  h3 {
    color: $green;
    font-size: em(18);
    line-height: 1.29;
    margin: 0 0 em(16);

    @include mq-min(em(800)) {
      font-size: rem(20); } }

  h4 {
    margin: 0 0 em(6); }

  h5 {
    font-family: $sans-serif;
    font-size: rem(13);
    font-weight: normal;
    letter-spacing: .1em;
    line-height: 1.29;
    margin: 0 0 em(6);
    text-transform: uppercase; }

  h6 {
    color: $gray;
    font-family: $sans-serif;
    font-size: rem(13);
    font-weight: normal;
    letter-spacing: .1em;
    margin: 0 0 em(6);
    text-transform: uppercase; }

  p {
    font-family: $monospace;
    font-size: rem(14); }


  a {
    text-decoration: underline; }

  // Docs Page
  .key {
    font-size: rem(15); }

  .default {
    border-bottom: 1px solid $off-white;
    border-top: 1px solid $off-white;
    color: $gray;
    display: block;
    font-size: rem(13);
    margin-bottom: rem(16);
    margin-top: rem(5);
    padding: rem(15) 0 rem(13);
    width: 100%;

    code {
      background: none; } } }



// ------------------------------------------------------------
// Lists
// ------------------------------------------------------------

ol,
ul {
  @extend p;
  margin-bottom: 1em;
 }  // padding-left: 1.3em

ul {
  list-style-type: disc; }

ol {
  list-style-type: decimal;
  // list-style-position: inside

  li {
    // padding-left: em(33)
 } }    // text-indent: em(-27)

li {
  // display: block
  line-height: 1.667;
  margin-bottom: em(10); }


ul.plain {
  list-style-type: none;
  padding: 0;

  li {
    line-height: 1.2;
    padding: 0; } }



// ------------------------------------------------------------
// Links
// ------------------------------------------------------------

a {
  color: $link-color;
  text-decoration: none;
  outline: 0;

  &:hover,
  &:focus {
    color: $link-hover-color; } }


.subnav {

  .headline {
    color: $green;
    margin-bottom: rem(30);
    border-bottom: 1px solid $off-white;
    padding-bottom: rem(10);

    &:not(:first-of-type) {
      padding-top: rem(30); } }

  .title {
    color: $dark-gray;
    display: block;
    margin-bottom: rem(12); }

  ul {
    max-width: rem(300);
    display: block;
    font-family: $monospace;
    font-size: rem(14);
    margin-bottom: rem(36);

    li {
      margin-bottom: rem(16);
      text-indent: rem(-15);
      margin-left: rem(15);
      padding-right: rem(15);

      &:before {
        content: '> '; } } } }





// ------------------------------------------------------------
// Titles
// ------------------------------------------------------------

.title {
  color: $green;
  font-size: rem(13);
  letter-spacing: .1em;
  text-transform: uppercase;

  &.-medium {
    font-size: rem(26); }

  &.-large {
    font-size: rem(36); } }


// ------------------------------------------------------------
// Headlines
// ------------------------------------------------------------

.headline,
.headline p {
  color: $dark-gray;
  font-size: rem(18);
  line-height: 1.3;

  &.-medium,
  &.-medium p {
    font-size: rem(21); }

  &.-large,
  &.-large p {
    font-size: rem(30); }

  &.-xlarge,
  &.-xlarge p {
    font-size: rem(42); } }

// ------------------------------------------------------------
// Intros
// ------------------------------------------------------------

.intro,
.intro p {
  font-family: $sans-serif;
  color: $dark-gray;
  font-size: rem(16);
  max-width: rem(500);

  &.-medium,
  &.-medium p {
    font-size: rem(20); }

  &.-large,
  &.-large p {
    font-size: rem(24); } }
