// ============================================================
// A plethora of media queries. See each media query mixin
// for the required parameters.
// ============================================================
// Example usage:
// .foo
//   background: url('../img/bg.png') top center no-repeat
//   width: 100%
//
//    +mq-min-width(37.5em)
//      float: left
//      width: 50%
//
//    +mq-2x
//      background-image: url('../img/bg@2x.png')
// ============================================================

@mixin mq-min($screen_width, $orientation:"") {
  @if $orientation == "" {
    @media only screen and (min-width: $screen_width) {
      @content; } }
  @else {
    @media only screen and (min-width: $screen_width) and (orientation: $orientation) {
      @content; } } }

@mixin mq-max($screen_width, $orientation:"") {
  @if $orientation == "" {
    @media only screen and (max-width: $screen_width) {
      @content; } }
  @else {
    @media only screen and (max-width: $screen_width) and (orientation: $orientation) {
      @content; } } }

@mixin mq-between($min_width, $max_width, $orientation:"") {
  @if $orientation == "" {
    @media only screen and (min-width: $min_width) and (max-width: $max_width) {
      @content; } }
  @else {
    @media only screen and (min-width: $min_width) and (max-width: $max_width) and (orientation: $orientation) {
      @content; } } }

@mixin mq-ipad($orientation:"landscape") {
  @media only screen and (device-width: 768px) and (device-height: 1024px) and (orientation:$orientation) {
    @content; } }


@mixin mq-2x {
  @media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (-moz-min-device-pixel-ratio: 2), only screen and (-o-min-device-pixel-ratio: 2/1), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
    @content; } }
