// ============================================================
// GRID
// ============================================================
// 1. Grid Item Mixin
// 2. Grid Item Base
// 3. Repeating Grids
// ------------------------------------------------------------
// Example Usage:
// <div class="g -half">
//   <div class="gi">...</div>
//   <div class="gi">...</div>
// </div>
// ============================================================


// ------------------------------------------------------------
// 1. Grid Item Mixin
// ------------------------------------------------------------

@mixin grid-item($columns, $grid-units:1, $grid-gutter:$grid-gutter, $grid-type:row, $grid-push:0) {
  margin-bottom: $grid-gutter;
  @if $columns > 1 {
    margin-right: $grid-gutter;
    width: calc(((100% / #{$columns}) * #{$grid-units}) - (#{$grid-gutter} / (#{$columns / #{$columns - 1 * $grid-units}}))); }
  @else {
    margin-right: 0;
    width: 100%; }

  @if $grid-push > 0 {
    margin-left: calc(((100% / #{$columns}) * #{$grid-push}) + (#{$grid-gutter} / #{$columns})); }

  @if $grid-type == repeat {
    &:nth-child(#{$columns}n) {
      margin-right: 0; } }
  @else {
    &:last-child {
      margin-right: 0; } } }

// ------------------------------------------------------------
// 2. Grid Item Base
// ------------------------------------------------------------

.gi {
  margin-bottom: $grid-gutter;
  width: 100%;

  &.f {
    display: flex;
    flex-wrap: wrap; } }

// ------------------------------------------------------------
// 3. Regular Grids
// ------------------------------------------------------------

.g {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  // --------------------
  // Full
  // --------------------
  &.-full > .gi {
    @include grid-item(1, $grid-type:repeat); }

  // --------------------
  // Half
  // --------------------
  &.-half > .gi {

    @include mq-min(em(600)) {
      @include grid-item(2, $grid-type:repeat); } }

  // --------------------
  // Thirds
  // --------------------
  &.-thirds > .gi {

    @include mq-min(em(600)) {
      @include grid-item(3, $grid-type:repeat); } }

  // --------------------
  // Fourths
  // --------------------
  &.-fourths > .gi {

    @include mq-between(em(600), em(900)) {
      @include grid-item(2, $grid-type:repeat); }

    @include mq-min(em(900)) {
      @include grid-item(4, $grid-type:repeat); } }

  // --------------------
  // Fifths
  // --------------------
  &.-fifths > .gi {

    @include mq-between(em(600), em(900)) {
      @include grid-item(3, $grid-type:repeat); }

    @include mq-min(em(900)) {
      @include grid-item(5, $grid-type:repeat); } }

  // --------------------
  // Sixths
  // --------------------
  &.-sixths > .gi {
    @include mq-between(em(600), em(900)) {
      @include grid-item(3, $grid-type:repeat); }

    @include mq-min(em(900)) {
      @include grid-item(6, $grid-type:repeat); } } }
